<template>
	<div>
		<div class="title">游戏列表</div>
		<div class="searchs">
			<div>
				<label for="gameType">
					游戏类型：<el-select clearable v-model="gameTypeSearch" placeholder="请选择登陆方式">
						<el-option
							id="gameType"
							v-for="item in gameTypes"
							:key="item"
							:value="item">
						</el-option>
					</el-select>
				</label>
				<label for="isHotGame">
					热门游戏：<el-select clearable v-model="isHotGameSearch" placeholder="请选择登陆方式">
						<el-option
							id="isHotGame"
							v-for="item in isHotGames"
							:key="item"
							:value="item">
						</el-option>
					</el-select>
				</label>
				<label for="gameClass">
					游戏种类：<el-select clearable v-model="gameClassSearch" placeholder="请选择登陆方式">
						<el-option
							id="gameClass"
							v-for="item in gameClasses"
							:key="item"
							:value="item">
						</el-option>
					</el-select>
				</label>
				<label for="hotTag">
					热门标签：<el-select clearable v-model="hotTagsSearch" placeholder="请选择用户状态">
						<el-option
							id="hotTag"
							v-for="item in hotTags"
							:key="item"
							:value="item">
						</el-option>
					</el-select>
				</label>
				<label for="isShow">
					是否显示：<el-select clearable v-model="isShowSearch" placeholder="请选择用户状态">
						<el-option
							id="isShow"
							v-for="item in isShows"
							:key="item"
							:value="item">
						</el-option>
					</el-select>
				</label>
			</div>
			<div class="mt25">
				<label for="searchType">
					内容查询：<el-select clearable v-model="searchType" placeholder="请选择查询类别">
						<el-option
							id="searchType"
							v-for="item in searchTypes"
							:key="item"
							:value="item">
						</el-option>
					</el-select>
				</label>
				<label for="searchContent">
					<el-input clearable id="searchContent" v-model="searchContent" placeholder="请输入要查询的内容"></el-input>
				</label>
				<label for="time">
					时间查询：<el-date-picker
						id="time"
						class="bothdate"
				    	v-model="time"
				    	type="datetimerange"
				    	range-separator="至"
				    	start-placeholder="开始日期"
				    	end-placeholder="结束日期"
				    	value-format="yyyy-MM-dd HH:mm:ss">
				    </el-date-picker>
				</label>
				<el-button class="mr25" type="primary" icon="el-icon-search" @click="search">查询</el-button>
				<el-button type="success" class="add_btn" @click="add">新增</el-button>
			</div>
		</div>
		<table class="main_table">
			<thead>
				<tr>
					<td>游戏ID</td>
					<td>游戏名称</td>
					<td>游戏类型</td>
					<td>热门游戏</td>
					<td>游戏种类</td>
					<td>热门标签</td>
					<td>运营代理商</td>
					<td>时间</td>
					<td>是否显示</td>
					<td>操作</td>
				</tr>
			</thead>
			<tbody>
				<tr v-for="(item,index) in gameList">
					<td>{{item.gameId}}</td>
					<td>{{item.gameName}}</td>
					<td>{{item.gameType}}</td>
					<td>{{item.isHot}}</td>
					<td>{{item.gameClass}}</td>
					<td>{{item.hotTag}}</td>
					<td>{{item.operateName}}</td>
					<td>{{item.onlineTime}}</td>
					<td>{{item.isShow}}</td>
					<td><el-button size="mini" type="warning" icon="el-icon-edit" @click="modify">修改</el-button></td>
				</tr>
			</tbody>
			<tfoot>
				<tr>
					<td colspan="11">
						<el-pagination
							background
							layout="prev, pager, next"
							:total="pages*10">
						</el-pagination>
					</td>
				</tr>
			</tfoot>
		</table>
		<!-- 新增游戏 -->
		<el-dialog
			title="游戏列表"
			:visible.sync="dialogShow"
			width="60%"
			center>
			<table class="mini">
				<caption>{{dialogType}}</caption>
				<tr>
					<td>游戏名称</td>
					<td><el-input clearable v-model="gameName" placeholder="请输入游戏名称"></el-input></td>
				</tr>
				<tr>
					<td>游戏图标</td>
					<td>
						<input id="game_icon" type="file" @change="change_file_img('game_icon','select_img1')">
						<img v-if="dialogType=='新增游戏'" class="select_img1" src="static/games_list/add.png" alt="hello world" @click="pic_click('game_icon')">
						<img v-if="dialogType=='修改游戏'" class="select_img1" src="static/games_list/add.png" alt="hello world" @click="pic_click('game_icon')">
					</td>
				</tr>
				<tr>
					<td>游戏类型</td>
					<td>
						<el-select multiple clearable v-model="gameType" placeholder="请选择游戏类型">
					    	<el-option
					    		v-for="item in gameTypes"
					    		:key="item"
					    		:value="item">
					    	</el-option>
					  	</el-select>
					</td>
				</tr>
				<tr>
					<td>热门游戏</td>
					<td>
						<el-select clearable v-model="isHotGame" placeholder="是否为热门游戏">
					    	<el-option
					    		v-for="item in isHotGames"
					    		:key="item"
					    		:value="item">
					    	</el-option>
					  	</el-select>
					</td>
				</tr>
				<tr>
					<td>游戏种类</td>
					<td>
						<el-select clearable v-model="gameClass" placeholder="请选择游戏种类">
					    	<el-option
					    		v-for="item in gameClasses"
					    		:key="item"
					    		:value="item">
					    	</el-option>
					  	</el-select>
					</td>
				</tr>
				<tr v-if="gameClass=='网页游戏'">
					<td>游戏链接</td>
					<td>
						<el-input clearable v-model="gameLink" placeholder="请输入游戏链接"></el-input>
					</td>
				</tr>
				<tr v-if="gameClass=='手机游戏'">
					<td>游戏二维码</td>
					<td>
						<input id="game_ewm" type="file" @change="change_file_img('game_ewm','select_img2')">
						<img v-if="dialogType=='新增游戏'" class="select_img2" src="static/games_list/add.png" alt="hello world" @click="pic_click('game_ewm')">
						<img v-if="dialogType=='修改游戏'" class="select_img2" src="static/games_list/add.png" alt="hello world" @click="pic_click('game_ewm')">
					</td>
				</tr>
				<tr>
					<td>热门标签</td>
					<td>
						<el-select multiple clearable v-model="hotTag" placeholder="请选择热门标签">
					    	<el-option
					    		v-for="item in hotTags"
					    		:key="item"
					    		:value="item">
					    	</el-option>
					  	</el-select>
					</td>
				</tr>
				<tr>
					<td>运营代理商</td>
					<td>
						<el-input clearable v-model="saleDepartment" placeholder="请输入运营代理商"></el-input>
					</td>
				</tr>
				<tr>
					<td>上线时间</td>
					<td>
						<label clearable for="showTime">
							上线时间：<el-date-picker
								id="showTime"
								v-model="showTime"
								type="datetime"
								placeholder="选择上线时间">
							</el-date-picker>
						</label>
					</td>
				</tr>
				<tr>
					<td>在线人数</td>
					<td>
						<el-input clearable disabled v-model="onlinePeoples" placeholder="请输入在线人数"></el-input>
					</td>
				</tr>
				<tr>
					<td>是否显示</td>
					<td>
						<el-select clearable v-model="isShow" placeholder="请选择是否显示">
					    	<el-option
					    		v-for="item in isShows"
					    		:key="item"
					    		:value="item">
					    	</el-option>
					  	</el-select>
					</td>
				</tr>
				<tr>
					<td>缩略展示图</td>
					<td>
						<input id="game_mini" type="file" @change="change_file_img('game_mini','select_img3')">
						<img v-if="dialogType=='新增游戏'" class="select_img3" src="static/games_list/add.png" alt="hello world" @click="pic_click('game_mini')">
						<img v-if="dialogType=='修改游戏'" class="select_img3" src="static/games_list/add.png" alt="hello world" @click="pic_click('game_mini')">
					</td>
				</tr>
				<tr>
					<td>游戏介绍</td>
					<td>
						<el-input
							class="textarea"
							type="textarea"
							:rows="2"
							placeholder="请输入游戏介绍"
							v-model="gameIntroduce">
						</el-input>
					</td>
				</tr>
				<tr>
					<td>技术亮点</td>
					<td>
						<el-input
							class="textarea"
							type="textarea"
							:rows="2"
							placeholder="请输入技术亮点"
							v-model="skillLighter">
						</el-input>
					</td>
				</tr>
				<tr>
					<td>游戏背景图片</td>
					<td>
						<input id="game_banner" type="file" @change="change_file_img('game_banner','select_img4')">
						<img v-if="dialogType=='新增游戏'" class="select_img4" src="static/games_list/add.png" alt="hello world" @click="pic_click('game_banner')">
						<img v-if="dialogType=='修改游戏'" class="select_img4" src="static/games_list/add.png" alt="hello world" @click="pic_click('game_banner')">
					</td>
				</tr>
			</table>
			<table class="mini">
				<tbody>
					<!-- 特色玩法 -->
					<tr>
						<td>特色玩法标题</td>
						<td colspan="2">跳转链接</td>
						<td>操作</td>
					</tr>
					<tr v-for="(item,index) in featureGameplay">
						<td>
							<el-input clearable v-model="featureGameplay[index].title" placeholder="请输入特色玩法标题"></el-input>
						</td>
						<td colspan="2">
							<el-input clearable v-model="featureGameplay[index].link" placeholder="请输入特色玩法跳转链接"></el-input>
						</td>
						<td>
							<el-button type="success" @click="public_add('featureGameplay',index)">新增</el-button>
							<el-button type="danger" @click="public_del('featureGameplay',index)">删除</el-button>
						</td>
					</tr>
					<!-- 角色信息 -->
					<tr>
						<td>角色图像</td>
						<td>角色名称</td>
						<td>角色描述</td>
						<td>操作</td>
					</tr>
					<tr v-for="(item,index) in roleInfos" class="roleInfos">
						<td>
							<input :id="`game_infoImg${index}`" type="file" @change="change_file_img(`game_infoImg${index}`,`select_imga_${index}`)">
							<img v-if="dialogType=='新增游戏'" :class="`select_imga_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`game_infoImg${index}`)">
							<img v-if="dialogType=='修改游戏'" :class="`select_imga_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`game_infoImg${index}`)">
							<!-- <input type="file" style="display:block"> -->
						</td>
						<td>
							<el-input clearable v-model="roleInfos[index].name" placeholder="请输入角色名称"></el-input>
						</td>
						<td>
							<el-input
								class="input-mini"
								type="textarea"
								:rows="2"
								placeholder="请输入角色描述"
								v-model="roleInfos[index].description">
							</el-input>
						</td>
						<td>
							<el-button type="success" @click="public_add('roleInfos',index)">新增</el-button>
							<el-button type="danger" @click="public_del('roleInfos',index)">重置</el-button>
						</td>
					</tr>
					<!-- 游戏壁纸 -->
					<tr>
						<td>游戏壁纸</td>
						<td colspan="2">游戏壁纸大图</td>
						<td>操作</td>
					</tr>
					<tr v-for="(item,index) in gameWallpaper" class="gameWallpaper">
						<td>
							<input :id="`game_pic_${index}`" type="file" @change="change_file_img(`game_pic_${index}`,`select_imgb_${index}`)">
							<img v-if="dialogType=='新增游戏'" :class="`select_imgb_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`game_pic_${index}`)">
							<img v-if="dialogType=='修改游戏'" :class="`select_imgb_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`game_pic_${index}`)">
						</td>
						<td colspan="2">
							<input :id="`game_pic_l_${index}`" type="file" @change="change_file_img(`game_pic_l_${index}`,`select_imgb_l_${index}`)">
							<img v-if="dialogType=='新增游戏'" :class="`select_imgb_l_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`game_pic_l_${index}`)">
							<img v-if="dialogType=='修改游戏'" :class="`select_imgb_l_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`game_pic_l_${index}`)">
						</td>
						<td>
							<el-button type="success" @click="public_add('gameWallpaper',index)">新增</el-button>
							<el-button type="danger" @click="public_del('gameWallpaper',index)">重置</el-button>
						</td>
					</tr>
					<!-- 游戏截图 -->
					<tr>
						<td>游戏截图</td>
						<td colspan="2">游戏截图大图</td>
						<td>操作</td>
					</tr>
					<tr v-for="(item,index) in gameScreenshot" class="gameScreenshot">
						<td>
							<input :id="`gameScreenshot${index}`" type="file" @change="change_file_img(`gameScreenshot${index}`,`select_imgc_${index}`)">
							<img v-if="dialogType=='新增游戏'" :class="`select_imgc_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`gameScreenshot${index}`)">
							<img v-if="dialogType=='修改游戏'" :class="`select_imgc_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`gameScreenshot${index}`)">
						</td>
						<td colspan="2">
							<input :id="`gameScreenshot_l_${index}`" type="file" @change="change_file_img(`gameScreenshot_l_${index}`,`select_imgc_l_${index}`)">
							<img v-if="dialogType=='新增游戏'" :class="`select_imgc_l_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`gameScreenshot_l_${index}`)">
							<img v-if="dialogType=='修改游戏'" :class="`select_imgc_l_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`gameScreenshot_l_${index}`)">
						</td>
						<td>
							<el-button type="success" @click="public_add('gameScreenshot',index)">新增</el-button>
							<el-button type="danger" @click="public_del('gameScreenshot',index)">重置</el-button>
						</td>
					</tr>
				</tbody>
			</table>
			<span slot="footer" class="dialog-footer">
				<el-button type="primary" @click="submit">提交</el-button>
				<el-button @click="dialogShow = false">关闭</el-button>
			</span>
		</el-dialog>
	</div>
</template>
<style scoped lang="styl">
</style>
<script>
export default{
	data(){
		return{
			gameList:[],//游戏列表数据
			page:1,//当前页数
			pages:0,//总页数
			time:'',//查询的时间
			gameTypeSearch:'',//搜索所选游戏类型
			isHotGameSearch:'',//搜索所选是否热门游戏
			gameClassSearch:'',//搜索所选游戏类型
			hotTagsSearch:'',//搜索所选热门标签
			isShowSearch:'',//搜索所选是否显示
			searchType:'',//当前查询的类别
			searchTypes:['游戏名称','运营代理商'],//所有查询的类别
			searchContent:'',//查询的内容
			dialogType:'',//模态框类型
			dialogShow:false,// 新增模态框
			gameName:'',//游戏名称
			gameType:[],//所选游戏类型
			gameTypes:['动作','射击','格斗','冒险','模拟','角色扮演','策略','音乐','休闲','体育','竞速'],//全部游戏类型
			isHotGame:'',//所选热门游戏情况
			isHotGames:['是','否'],//全部热门游戏情况
			gameClass:'',//所选游戏种类
			gameClasses:['网页游戏','手机游戏'],//全部游戏种类
			gameLink:'',//游戏链接
			hotTag:[],//所选热门标签
			hotTags:['棋牌','篮球'],//全部热门标签
			saleDepartment:'',//运营代理商
			showTime:'',//上线时间
			onlinePeoples:'',//在线人数
			isShow:'',//所选是否显示
			isShows:['是','否'],//全部是否显示情况,
			gameIntroduce:'',//游戏介绍
			skillLighter:'',//技术亮点
			featureGameplay:[
				{
					title:'',//特色玩法标题
					link:''//特色玩法链接
				}
			],//特色玩法
			roleInfos:[
				{
					name:'',//角色名称
					description:''//角色描述
				}
			],//角色信息
			gameWallpaper:[0],//游戏壁纸数量
			gameScreenshot:[0],//游戏截图数量
			textarea:''
		}
	},
	created(){
		this.getGameList();
	},
	methods:{
		// 获取后台游戏列表数据
		getGameList(){
			this.$axios.get(`/infos/game_list_get
				?page=${this.page}
				&gameType=${this.gameTypeSearch}
				&isHot=${this.isHotGameSearch}
				&gameClass=${this.gameClassSearch}
				&hotTag=${this.hotTagsSearch}
				&isShow=${this.isShowSearch}
				&searchType=${this.searchType}
				&searchContent=${this.searchContent}
				&time=${this.time}
				`).then((res)=>{
				this.gameList=res.data.rows;
				this.pages=res.data.pages;
			}).catch((err)=>{
				console.log(err);
			});
		},
		// 查询
		search(){
			// 前端类型验证
			if(this.searchType==''&&this.searchContent!=''){
				this.$message({
		          	message: `您还未选择查询类别`,
		          	type: 'warning'
		        });
			}else{
				this.getGameList();
			}
		},
		// 新增
		add(){
			// 模态框打开类型为新增
			this.dialogShow=true;
			this.dialogType='新增游戏';
			// 初始化模态框
			this.modal_init();
		},
		// 修改
		modify(){
			this.dialogShow=true;
			this.dialogType='修改游戏';
			// 初始化模态框
			this.modal_init();
		},
		// 模态框初始化
		modal_init(){
			// 所有数据初始化
			$('img').attr('src','static/games_list/add.png');
			$('input:file').val('');
			this.gameName='';
			this.gameType=[];
			this.isHotGame='';
			this.gameClass='';
			this.gameLink='';
			this.hotTag=[];
			this.saleDepartment='';
			this.showTime='';
			this.onlinePeoples='';
			this.isShow='';
			this.gameIntroduce='';
			this.skillLighter='';
			this.featureGameplay=[{title:'',link:''}];
			this.roleInfos=[{name:'',description:''}];
		},
		// 表格新增
		public_add(source,index){
			switch(source){
				case 'featureGameplay':var obj={title:'',link:''};break;
				case 'roleInfos':var obj={name:'',description:''};break;
				case 'gameWallpaper':var obj={};break;
			}
			this[source].splice(index+1,0,obj);
		},
		// 表格删除/重置
		public_del(source,index){
			if(source=='gameWallpaper'){
				$(`#game_pic_${index}`).val('');
				$(`#game_pic_l_${index}`).val('');
				$(`.select_imgb_${index}`).attr('src','static/games_list/add.png');
				$(`.select_imgb_l_${index}`).attr('src','static/games_list/add.png');
			}else if(source=='gameScreenshot'){
				$(`#gameScreenshot_${index}`).val('');
				$(`#gameScreenshot_l_${index}`).val('');
				$(`.select_imgc_${index}`).attr('src','static/games_list/add.png');
				$(`.select_imgc_l_${index}`).attr('src','static/games_list/add.png');
			}else if(source=='roleInfos'){
				$(`#game_infoImg${index}`).val('');
				$(`.select_imga_${index}`).attr('src','static/games_list/add.png');
				this.roleInfos[index].name='';
				this.roleInfos[index].description='';
			}else{
				if(this[source].length>1){
					this[source].splice(index,1);
				}else{
					this.$message({
			        	message: '至少得有一条数据,删除失败',
			        	type: 'warning'
			        });
				}
			}
		},
		// 提交上传
		submit(){
			this.dialogShow=false;
			// 上传图片--游戏图标
			this.on_click_upload_file('game_icon','icon');
			if(this.gameClass=='手机游戏'){
				// 上传图片--游戏二维码
				this.on_click_upload_file('game_ewm','ewm');
			}
			//上传图片--缩略展示图
			this.on_click_upload_file('game_mini','mini');
			// 上传图片--游戏背景图
			this.on_click_upload_file('game_banner','background');
			//上传图片--角色图像
			var length=$('.roleInfos').length;
			for(var i=0;i<length;i++){
				this.on_click_upload_file(`game_infoImg${i}`,'role');
			}
			// 上传图片--游戏壁纸和游戏壁纸大图
			var length=$('.gameWallpaper').length;
			for(var i=0;i<length;i++){
				this.on_click_upload_file(`game_pic_${i}`,'wallpaper');
				this.on_click_upload_file(`game_pic_l_${i}`,'wallpaper_large');
			}
			// 上传图片--游戏截图和游戏截图大图
			var length=$('.gameScreenshot').length;
			for(var i=0;i<length;i++){
				this.on_click_upload_file(`gameScreenshot${i}`,'screenshot');
				this.on_click_upload_file(`gameScreenshot_l_${i}`,'screenshot_large');
			}
			var str=this.dialogType.substring(0,2);
			this.$message({
	          	message: `${str}成功！`,
	          	type: 'success'
	        });
		},
		// 点击图片、弹出文件选择
		pic_click(id){
			$(`#${id}`).click();
		},
		// 文件选择完毕、触发事件、改变所选图片
		change_file_img(_id,type){
			var preview=document.querySelectorAll(`.${type}`)[0];
			var file=document.getElementById(_id).files[0];
			var reader=new FileReader();
			reader.onloadend=()=>{
                preview.src=reader.result;
            }
            if(file){
                reader.readAsDataURL(file);
            }
		},
		// 图片上传到ali-oss
		on_click_upload_file(file_id,file_path){
			// 上传图片
			var client = new OSS.Wrapper({
		        region: 'oss-cn-shenzhen',
		        accessKeyId: 'LTAIaGX9gQ9iRNyJ',
		        accessKeySecret: '4NoCWvII2zAdM3xFNCFjNxxjzeILbb',
		        bucket:'brand-new-days'
		    });
			var f = document.getElementById(file_id).files[0];
	    	var val= document.getElementById(file_id).value;
	  		// 如果值为空、则不上传
	    	if(val==''){
				return;
			}
	    	var obj=`${file_path}/${f.name}`;  // 这里是生成文件名
	    	var storeAs = obj;  //命名空间加上文件名,中间加上/ 会在后台生成文件夹(如果有crate权限)
	    	console.log(' => ' + storeAs);
	    	client.multipartUpload(storeAs, f).then(function (result) {
	    		console.log(result);
	    		console.log(result.url);
	    	}).catch(function (err) {
	    	    console.log(err);
	    	});
		}
	}
}
</script>


